import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../../services/common.service';
import { TranslateService } from '@ngx-translate/core';
import { Injectable, Inject } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
//import { NgxQrcodeElementTypes, NgxQrcodeErrorCorrectionLevels } from '@techiediaries/ngx-qrcode';
@Component({
  selector: 'app-printdriverdetail',
  templateUrl: './printdriverdetail.component.html',
  styleUrls: ['./printdriverdetail.component.scss']
})
@Injectable({
  providedIn: 'root'
})
export class PrintdriverdetailComponent implements OnInit {
  value: any;
  APIBASEURL: string;
  elementType: any;
  mobile: any; data: boolean = false;
  correctionLevel: any; GUIDN: string = "";
  driverLatterInfo: any = [];
  driverDataforLatter: any = [];
  //isReport: boolean = false;
  constructor(
    private apiCall: ApiService,
    public common: CommonService,
    private translate: TranslateService,
    @Inject('BASE_URL') baseUrl: string
  ) {
    this.APIBASEURL = baseUrl;
  }

  async ngOnInit() {
    debugger;
    let isListprint = this.common.getValueFromSessionStorage('listdriverPrint');
    if (isListprint) {
      this.driverLatterInfo = this.common.getValueFromSessionStorage('driverDataforLatter');
      window.print();
    }
    else {
      this.GUIDN = window.location.href.split('/').pop();
      let jsonData: any = {};
      this.data = false;
      let letter: any = window.location.href;
      jsonData.DriverGuid = this.GUIDN;
      await this.apiCall.postAPICallWithToken('api/EnquiryManager/DriverLetters', jsonData).then(result => {
        debugger;
        this.driverLatterInfo = result;
        //this.elementType = NgxQrcodeElementTypes.URL;
        //this.correctionLevel = NgxQrcodeErrorCorrectionLevels.HIGH;
        //var str = "driverEnquiry";
        //var url = letter.replace("printdriverdetail", str);
       // this.value = 'http://localhost:61122/print/driverEnquiry/' + this.GUIDN; //url;// 'https://smartportaluat.azurewebsites.net/print/printcompanydetail/' + this.GUIDN;
        this.value = window.location.href;// 'http://localhost:61122/print/printdriverdetail/' + this.GUIDN;
        setTimeout(() =>
          window.print()
          , 1000);
      }).catch(error => {
        console.log("error ", error);
      });
    }

  }

}
